/* Global */

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  width: 100%;
  height: 100%;
}

body {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0; /* reset */
  background-color: #000;
  background-image: url('./assets/wallpaper.jpg');
  background-size: cover;
  overflow: hidden;
}

body::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-color: rgba(0, 0, 0, 0.5);
}

/* Stuff */

.container {
  min-height: 100%;
  min-width: 100%;
  display: grid;
  grid-template-rows: 1fr 128px;
  grid-template-columns: 100%;
}

.logo {
  align-self: center;
  justify-self: center;
  filter: drop-shadow(0 3px 9px rgba(255, 255, 255, 0.25));
}

.loading-bar {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(18, 18, 18, 0.8);
  backdrop-filter: blur(33px);
}

/* Loading spinner configuration */

:root {
  /* Variables */
  --loading-spinner__block-size: 16px;
  --loading-spinner__background-color: #00fde2;
  --loading-spinner__glow: 0.33;
  --loading-spinner__pulse-scale: 0.88;
  --loading-spinner__pulse-background-color: #5e6464;
  --loading-spinner__pulse-glow: 0.1;
  --loading-spinner__animation-length: 6s;

  /* Derived variables */
  --loading-spinner__block-diagonal-diff: calc(var(--loading-spinner__block-size) - (var(--loading-spinner__block-size) * 1.41 / 2));
}

/* Loading spinner */

@keyframes animation__loading-spinner {
  0% {
    transform: none;
  }

  18.054% {
    transform: rotate(0);
  }
  22.224% {
    transform: rotate(140deg);
  }
  25% {
    transform: rotate(135deg);
  }

  52.77% {
    transform: rotate(135deg);
  }
  52.771% {
    transform: rotate(0);
  }

  76.376% {
    transform: scale(1);
    transition-timing-function: cubic-bezier(0.76, 0, 0.24, 1);
  }
  76.377% {
    transform: scale(2);
  }
  88.044% {
    transform: scale(1);
  }

  100% {
    transform: none;
  }
}

.loading-spinner {
  position: relative; /* has "position: absolute" children */
  width: calc(var(--loading-spinner__block-size) * 3);
  height: calc(var(--loading-spinner__block-size) * 3);
  perspective: calc(var(--loading-spinner__block-size) * 3);
  animation: animation__loading-spinner var(--loading-spinner__animation-length) ease-in-out infinite;
  will-change: transform;
}

/* Loading spinner block */

.loading-spinner__block {
  position: absolute;
  width: var(--loading-spinner__block-size);
  height: var(--loading-spinner__block-size);
  background-color: var(--loading-spinner__background-color);
  box-shadow: 0 1px calc(var(--loading-spinner__block-size) * 0.5) rgba(255, 255, 255, var(--loading-spinner__glow));
  transform-style: preserve-3d;
  will-change: background-color, box-shadow, transform, transform-origin;
}

